import React, {  useState } from 'react';
import "./styleKH.css";
function Card(props) {
    const [isShow, setIsShow] = useState(false);
    let khoi = null;
    if (isShow) {
        khoi = (
            <div className="d-block" >
                <p><span>Id V:</span> {props.idve}</p>
                <p><span>Gi bt u:</span> {props.giobatdau}</p>
                <p><span>Tng gi v:</span> {props.giaVe}</p>
            </div>
        );
    } else {
        khoi = (<div></div>);
    }
    return (
        <div className="" style={{ borderBottom: '1px solid #BC5014', width: '600px' }}>
            <div className="d-flex align-content-center justify-content-between pb-3">
                <div className="d-flex">
                    {/* <div className="hehe" style={{ width: '85px', height: '85px' }}>
                        <img src="" className="w-100 rounded" alt="" />
                    </div> */}
                    <p className="font-weight-bold" style={{ marginTop: '2px' }}><span>Phim:</span> {props.tenphim}</p>
                </div>
                <div>
                    <p className="font-weight-bold" style={{ marginTop: '2px', color: 'rgba(0, 0, 0, 0.6)' }}><span>Xem ngy:</span> {props.ngay}</p>
                </div>
                <div>
                    <button type='button' className="btn btn-waring" style={{ marginTop: '2px', color: '#0000FF' }} onClick={() => { setIsShow(!isShow) }}>Chi tit</button>
                </div>
            </div>
            {khoi}
        </div >
    );
}
export default Card;
