import React, { useEffect, useState } from 'react';
import { useLocation } from 'react-router-dom';
import $ from 'jquery';
import { useContext } from 'react';
import '../App.css';
import AppProvider, { AppContext } from '../Context/AppProvider';
function HoaDon() {
    let data = useLocation();
    const [result,setResult] = useState({});
    const Server = useContext(AppContext);
    const [tp,setTp] = useState([]);
    console.log(data);
    // console.log(ip);
    // console.log(`http://${Server.data.ip}:8484/api/ve/getVe`);
    // console.log(data.pathname.split("/")[2]);
    useEffect(()=>{
        $.ajax({
            type: "get",
            async:false,
            url: `http://${Server.data.ip}:8484/api/ve/getVe`,
            data: {idVe:data.pathname.split("/")[2]},
            dataType: "json",
            success: function (response) {
                // console.log(response);
                setResult(response);
            },error:function (e){
                console.log(e);
            }
        });
        $.ajax({
            type: "get",
            async:false,
            url: `http://${Server.data.ip}:8484/api/order/getOrder`,
            data: {idVe:data.pathname.split("/")[2]},
            dataType: "json",
            success: function (response) {
                if(response !=""){
                    console.log(response);
                    setTp(response);

                }
            },error:function (e){
                console.log(e);
            }
        });
    },[]);
    let temp =null;
    if(tp !=null){
        temp = tp.map(s=>{
            return <div>
                <hr/>
                <p>Topping: {s.tenTopping}</p>
                <p>S lng: {s.soLuongMua}</p>
                </div>
        })
    }
    let maloai = null;
    if(result.tenloai=="Thng"){
        maloai = "success";
    }else if(result.tenloai =="Vip"){
        maloai = "danger";
    }else{
        maloai = "warning";
    }
    return ( 
         <div id='divShowVe' className='container-fluid border d-block mx-auto ' style={{marginTop:"30px", width:"70%"}}>
        <img src={`http://${Server.data.ip}:8484/Image/qrCode/${data.pathname.split("/")[2]}.jpg`}></img>
        <h1>Thng tin v</h1>
        <p>Tn khch hng: {result.tenkh}</p>
        <p>Ni xem: T1 Cinema - {result.diachi}</p>
        <hr/>
        <img className='d-block mx-auto' src={`http://${Server.data.ip}:8484/Image/poster/${result.hinh}`} height="300px"/>
        <p>Loi v: <span className={`btn btn-${maloai}`}>{result.tenloai}</span> </p>
        <p>Hng gh: {result.tenghe}</p>
        <p>Tn Phim: {result.tenPhim}</p>
        <p>Gi v: {(result.giaVe)}</p>
        <p>Ngy chiu: {result.giochieu}</p>
        {temp}
    </div>
);
}

export default HoaDon;
